.nav-bar-background {
  width: 100vw;
  background-color: #5457a2;
  position: relative;
}

.nav-bar-logo-container {
  margin-right: 4em;
}

.nav-bar-logo {
  width: 7.5em;
  height: 100%;
  position: absolute;
  z-index: 100;
  cursor: pointer;
}

.nav-bar-background-cut-out-circle {
  width: 100vw;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
}

.nav-bar-background-cut-out-rect {
  width: 100vw;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
}

.nav-bar-titles-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

.nav-bar-title {
  color: #1e203f;
  position: relative;
  display: inline-block;
  padding-bottom: 0.2em;
}

.nav-bar-title::before {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 0.1em;
  bottom: 0;
  left: 0;
  background-color: #ffd4b4;
  transform-origin: bottom left;
  transition: all 0.2s ease-in-out 0s;
}

.nav-bar-title:hover {
  color: #ffd4b4;
  transition: ease-in-out 0.3s;
  cursor: pointer;
}

.nav-bar-title:hover::before {
  transform: scaleX(1);
  transform-origin: bottom right;
}

.nav-bar-line-separator {
  margin-top: 0.2em;
  width: 100%;
  height: 0.2em;
  background-color: #ffd4b4;
}

